@import '../../_variables.scss';
.films {
  margin-top: 70px;
  padding: 20px;
  background: #000;
  &__wrapper {
    display: flex;
    gap: 40px;
    flex-wrap: wrap;
    margin-bottom: 50px;
    @media (max-width: 769px) {
      gap: 20px;
      justify-content: space-around;
    }
  }
  &__link {
    display: block;
    width: 200px;
    height: 300px;
  }
  &__pagination {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__loadMore {
    padding: 12px 22px;
    border-radius: 18px;
    -webkit-border-radius: 18px;
    -moz-border-radius: 18px;
    -ms-border-radius: 18px;
    -o-border-radius: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: $mainСolor;
    background-color: rgb(230, 93, 14);
    font-size: 16px;
    transition: all 0.3s;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    &:hover {
      background-color: rgb(192, 78, 11);
      color: #fff;
    }
  }
}
.filmCard {
  position: relative;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  max-width: 200px;
  height: 100%;
  height: 300px;
  outline: 1px red solid;
  overflow: hidden;
  @media (max-width: 769px) {
    max-width: 100px;
  }
  &__img {
    margin-bottom: 10px;
    overflow: hidden;
    border-radius: 10px;
    img {
      border-radius: 10px;
      width: 100%;
      height: 100%;
      object-fit: contain;
      transition: all 0.4s;
      -webkit-transition: all 0.4s;
      -moz-transition: all 0.4s;
      -ms-transition: all 0.4s;
      -o-transition: all 0.4s;
      -webkit-border-radius: 10px;
      -moz-border-radius: 10px;
      -ms-border-radius: 10px;
      -o-border-radius: 10px;
      &:hover {
        transform: scale(1.1);
        -webkit-transform: scale(1.1);
        -moz-transform: scale(1.1);
        -ms-transform: scale(1.1);
        -o-transform: scale(1.1);
      }
    }
  }
  &__rating {
    position: absolute;
    top: 5%;
    left: 5%;
    padding: 5px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    background-color: rgb(227, 77, 22);
    color: #fff;
    z-index: 10;
  }
  &__name {
    font-weight: 500;
    color: #fff;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-bottom: 5px;
    @media (max-width: 769px) {
      font-size: 12px;
    }
  }
  &__length {
    color: #fff;
    font-size: 12px;
  }
  &__info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }
}
