.result {
  padding: 100px 15px 50px 15px;
  background-color: #e1e0e0;
  height: 100%;
  &__title {
    margin-bottom: 30px;
  }
  &__wrapper {
    background-color: #fff;
    max-width: 1000px;
    margin: 0 auto;
    -webkit-box-shadow: 0px -2px 22px 0px rgba(34, 60, 80, 0.2);
    -moz-box-shadow: 0px -2px 22px 0px rgba(34, 60, 80, 0.2);
    box-shadow: 0px -2px 22px 0px rgba(34, 60, 80, 0.2);
  }
  &__cards {
    padding: 15px;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  &__nothing {
    font-size: 24px;
    margin-bottom: 20px;
  }
  &__back {
    padding: 12px 22px;
    border-radius: 18px;
    -webkit-border-radius: 18px;
    -moz-border-radius: 18px;
    -ms-border-radius: 18px;
    -o-border-radius: 18px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: rgba(255, 255, 255, 0.9019607843);
    background-color: rgb(230, 93, 14);
    font-size: 16px;
    transition: all 0.3s;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    &:hover {
      background-color: rgb(192, 78, 11);
      color: #fff;
    }
  }
}
.card {
  max-height: 200px;
  display: flex;
  align-items: center;
  gap: 20px;
  &:not(:last-child) {
    border-bottom: 1px solid #0000001b;
    padding-bottom: 5px;
  }

  &__info {
    flex: 0 1 75%;
    color: #000;
  }
  &__img {
    flex: 0 1 12%;
    overflow: hidden;
    border-radius: 10px;
    @media (max-width: 500px) {
      flex: 0 1 17%;
    }
    img {
      border-radius: 10px;
      width: 100%;
      height: 100%;
      object-fit: contain;
      transition: all 0.4s;
      -webkit-transition: all 0.4s;
      -moz-transition: all 0.4s;
      -ms-transition: all 0.4s;
      -o-transition: all 0.4s;
      -webkit-border-radius: 10px;
      -moz-border-radius: 10px;
      -ms-border-radius: 10px;
      -o-border-radius: 10px;
      &:hover {
        transform: scale(1.1);
        -webkit-transform: scale(1.1);
        -moz-transform: scale(1.1);
        -ms-transform: scale(1.1);
        -o-transform: scale(1.1);
      }
    }
  }
  &__name {
    font-weight: 500;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-bottom: 10px;
    color: #000;
    transition: color 0.3s;
    -webkit-transition: color 0.3s;
    -moz-transition: color 0.3s;
    -ms-transition: color 0.3s;
    -o-transition: color 0.3s;
    &:hover {
      color: #636262;
    }
  }
  &__description {
    -webkit-line-clamp: 3;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    color: #000;
    transition: color 0.3s;
    -webkit-transition: color 0.3s;
    -moz-transition: color 0.3s;
    -ms-transition: color 0.3s;
    -o-transition: color 0.3s;
    &:hover {
      color: #636262;
    }
  }
}
