@import '../../_variables';

.footer {
  padding: 50px 0px;
  background-color: #141414;
  color: $mainСolor;
  &__wrapper {
    max-width: 1300px;
    margin: 0 auto;
    padding: 0px 15px;
  }
  &__top {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__info {
    display: flex;
    gap: 25px;
    margin-bottom: 50px;
    flex-wrap: wrap;
  }
  &__link {
    color: $secondColor;
    &:hover {
      color: $mainСolor;
    }
  }
  &__bottom {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__copy {
    color: $secondColor;
  }
  &__logo {
    color: $secondColor;
    &:hover {
      color: $mainСolor;
    }
  }
}
.iconSocial {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
  &__link {
    i {
      font-size: 26px;
      color: $secondColor;
      transition: color 0.3s ease;
      -webkit-transition: color 0.3s ease;
      -moz-transition: color 0.3s ease;
      -ms-transition: color 0.3s ease;
      -o-transition: color 0.3s ease;
      &:hover {
        color: $mainСolor;
      }
    }
  }
}
