@import '../../_variables';
.profile {
  &__action {
    display: flex;
    align-items: center;
    gap: 10px;
    @media (max-width: 769px) {
      gap: 0;
    }
  }
  &__btn {
    color: $secondColor;
    background: none;
    font-size: 14px;
    padding-top: 3px;
    transition: color 0.3s ease;
    -webkit-transition: color 0.3s ease;
    -moz-transition: color 0.3s ease;
    -ms-transition: color 0.3s ease;
    -o-transition: color 0.3s ease;

    &:hover {
      color: #ffff;
    }
    @media (max-width: 769px) {
      font-size: 10px;
    }
  }
  &__user {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__book {
    color: inherit;
    transition: all 0.3s;
    padding-right: 15px;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    &:hover {
      color: $secondColor;
    }
  }
  &__book_text {
    @media (max-width: 769px) {
      font-size: 0;
    }
  }
  &__userName {
    font-size: 14px;
    color: $secondColor;
    border-bottom: 1px solid #a6a2a260;
    padding-bottom: 3px;
    @media (max-width: 769px) {
      font-size: 10px;
    }
  }
}
