.bookmarks {
  max-width: 1000px;
  margin: 0 auto;
  padding: 100px 15px 15px 15px;

  &__link {
    margin-bottom: 10px;
  }
  &__center {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 100px;
  }
  &__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    margin-top: 30px;
  }
}
